"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { Button } from "@/components/ui/button"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { ChevronDown, LogOut, User, Settings } from "lucide-react"
// import Image from "next/image" // Not strictly needed unless you use the logo

const navigationItems = [
  { href: "/dashboard", label: "Dashboard", icon: "ðŸ“Š", section: "main" },
  { href: "/products", label: "Products", icon: "ðŸ“¦", section: "main" },
  { href: "/receipts", label: "Receipts", icon: "ðŸ“¥", section: "operations", badge: "3" }, // TODO: Connect badge to stats.pending_receipts
  { href: "/deliveries", label: "Deliveries", icon: "ðŸ“¤", section: "operations", badge: "2" },
  { href: "/transfers", label: "Transfers", icon: "ðŸ”„", section: "operations" },
  { href: "/adjustments", label: "Adjustments", icon: "âš™ï¸", section: "inventory" },
  { href: "/history", label: "Move History", icon: "ðŸ“œ", section: "inventory" },
  { href: "/settings", label: "Settings", icon: "âš¡", section: "system" },
]

export function Sidebar() {
  const pathname = usePathname()

  const handleLogout = () => {
    // Clear Auth Data
    localStorage.removeItem('token')
    localStorage.removeItem('user_id')
    // Redirect to Login
    window.location.href = "/"
  }

  return (
    <aside className="w-64 bg-background border-r border-border flex flex-col h-screen fixed left-0 top-0 z-50">
      {/* Logo Section */}
      <div className="p-6 border-b border-border flex items-center gap-3">
        {/* Replaced Image with a simple div if logo file is missing, or use <img> for compatibility */}
        <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center text-primary-foreground font-bold">
          Q
        </div>
        <span className="text-xl font-bold tracking-tight text-foreground">QuickTrace</span>
      </div>

      {/* Navigation Items */}
      <nav className="flex-1 p-4 space-y-6 overflow-y-auto">
        {/* Main Section */}
        <div>
          <p className="px-4 text-xs font-semibold text-muted-foreground uppercase tracking-wider mb-2">
            Main
          </p>
          <div className="space-y-1">
            {navigationItems.filter(i => i.section === 'main').map((item) => (
              <NavItem key={item.href} item={item} pathname={pathname} />
            ))}
          </div>
        </div>

        {/* Operations Section */}
        <div>
          <p className="px-4 text-xs font-semibold text-muted-foreground uppercase tracking-wider mb-2">
            Operations
          </p>
          <div className="space-y-1">
            {navigationItems.filter(i => i.section === 'operations').map((item) => (
              <NavItem key={item.href} item={item} pathname={pathname} />
            ))}
          </div>
        </div>

        {/* Inventory Section */}
        <div>
          <p className="px-4 text-xs font-semibold text-muted-foreground uppercase tracking-wider mb-2">
            Inventory
          </p>
          <div className="space-y-1">
            {navigationItems.filter(i => i.section === 'inventory').map((item) => (
              <NavItem key={item.href} item={item} pathname={pathname} />
            ))}
          </div>
        </div>
      </nav>

      {/* User Profile Footer */}
      <div className="p-4 border-t border-border">
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button 
              variant="ghost" 
              className="w-full justify-between text-foreground hover:bg-accent transition-all"
            >
              <div className="flex items-center gap-3">
                <div className="w-8 h-8 rounded-full bg-primary flex items-center justify-center text-primary-foreground font-bold text-sm flex-shrink-0">
                  JD
                </div>
                <div className="text-left min-w-0">
                  <p className="text-sm font-medium truncate">John Doe</p>
                  <p className="text-xs text-muted-foreground truncate">Inventory Mgr</p>
                </div>
              </div>
              <ChevronDown className="w-4 h-4 flex-shrink-0 text-muted-foreground" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end" className="w-56">
            <DropdownMenuItem className="cursor-pointer">
              <User className="w-4 h-4 mr-2" />
              <span>My Profile</span>
            </DropdownMenuItem>
            <DropdownMenuItem className="cursor-pointer">
              <Settings className="w-4 h-4 mr-2" />
              <span>Settings</span>
            </DropdownMenuItem>
            <DropdownMenuItem onClick={handleLogout} className="cursor-pointer text-destructive focus:text-destructive">
              <LogOut className="w-4 h-4 mr-2" />
              <span>Logout</span>
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </div>
    </aside>
  )
}

// Helper Component for Menu Items
function NavItem({ item, pathname }: { item: any, pathname: string }) {
  const isActive = pathname.startsWith(item.href)
  
  return (
    <Link
      href={item.href}
      className={`flex items-center justify-between px-4 py-2.5 rounded-md transition-colors text-sm font-medium ${
        isActive 
          ? "bg-primary/10 text-primary" 
          : "text-muted-foreground hover:bg-accent hover:text-foreground"
      }`}
    >
      <div className="flex items-center gap-3">
        <span className="text-lg">{item.icon}</span>
        <span>{item.label}</span>
      </div>
      {item.badge && (
        <span className="bg-primary text-primary-foreground text-[10px] px-1.5 py-0.5 rounded-full font-bold">
          {item.badge}
        </span>
      )}
    </Link>
  )
}